@import '../../styles/variables.scss';

.order-card {
  padding: 10px 20px;
  width: 100%;
  background-color: #fff;

  .order-title {
    span {
      display: inline-block;
      padding-top: 4px;
    }
  }

  .orders-body {
    padding: 0 5px;

    .subtitle {
      font-weight: 600;
      font-size: 13px;
    }
  }

  .products-verify-body {
    .subtitle {
      font-weight: 400;
    }

    .value {
      font-weight: 500;
      margin-top: 2px;
    }
  }
}

.product-card {
  padding: 10px 20px;
  width: 100%;
  background-color: #fff;
  font-size: 14px;
  box-shadow: none;
  &.shadow{
    box-shadow: 0px 2px 8px rgba(28, 28, 28, 0.08);
  }
  .product-title {
    display: flex;
    align-items: center;
    width: 100%;
    padding-top: 4px;
    min-height: 29px;

    .name {
      flex-grow: 15;
    }

    .qty {
      flex-grow: 1;
    }
  }

  .product-body {
    padding: 0 15px;

    .subtitle {
      font-weight: 600;
      font-size: 13px;
      margin: 0 15px;
    }

    .right-value {
      margin-top: 14px;
    }
  }

  .orders-body {
    padding: 0 5px;
    max-height: 464px;
    transition: all 0.4s ease;
    overflow: hidden;
  }

  .collapsed-body {
    padding: 0 5px;
    max-height: 0;
    transition: all 0.5s ease;
    overflow: hidden;
  }
}

.product-card-disable{
  opacity: 0.5;
}

.phone-card {
  padding: 10px 20px 20px 20px;
  margin: 0px 0px 10px 0px;
  width: 100%;
  background-color: #fff;

  .phone-title {
    span {
      display: inline-block;
      padding-top: 4px;
    }
  }

  .phone-body {
    padding: 0 5px;

    .subtitle {
      font-weight: 600;
      font-size: 12px;
    }

    .right-value {
      margin-top: 14px;
    }
  }
}

.order-top-header {
  padding: 0 20px;
}

.verify-top-header {
  width: 100%;
  display: flex;
  padding: 0 20px;
  align-items: center;
  justify-content: space-between;
}

.order-filter-wrapper {
  width: 100%;
  overflow-x: scroll;

  .order-filter-wrapper_inner {
    width: 100%;
    text-align: left;
    padding: 0;
    width: 340px;
    margin: 0;
    font-size: 12px;
    padding-bottom: 10px;
    color: $dark-gray;

    div {
      background-color: #fff;
      font-size: 14px;
      padding: 5px 25px;
      border-radius: 4px;
      margin: 0;
      cursor: pointer;
      position: relative;
      margin-left: 8px;
      color: $med-gray;
      border: 1px solid $sushi-gray-border;
    }
  }
}

.order-photos {
  width: 100%;

  .order-photo-wrapper {
    position: relative;
    display: inline-block;
    margin: 10px 10px 10px 0;
  }

  .order-photo-ele {
    width: 80px;
    height: 80px;
    display: inline-block;
    overflow: hidden;
    border-radius: 4px;
    position: relative;
    vertical-align: middle;
    background-size: cover;
    background-position: center;
    border: 0.5px solid #D1D1D1;

    img {
      width: 100%;
    }
  }

  .order-photo-placeholder {
    background-color: #F4F4F4;
    text-align: center;
    padding: 30px;
    display: inline-block;

    svg {
      font-size: 18px;
      color: #9C9C9C;
    }
  }

  .red-order-photo-placeholder {
    border: 1px solid $pri-red;

    svg {
      color: $pri-red;
    }
  }

  .cross-image {
    width: 17px;
    height: 17px;
    background-color: $red;
    color: #fff;
    text-align: center;
    font-size: 9px;
    padding-top: 3px;
    border-radius: 50%;
    position: absolute;
    right: -5px;
    z-index: 1;
    top: -7px;

  }
}

.approval-wrap {
  background-color: #fff;
  width: 100%;
  padding: 10px 20px;
  position: fixed;
  bottom: 0;
  box-shadow: 0px 0px 30px rgba(196, 209, 225, 0.3);
}

.fixed-verify-section {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  left: 0;
  box-shadow: 0 0 8px $light-gray;
  transition: all 0.3s ease-in-out;

  &.toggleDown {
    bottom: -70px;
  }

  &.flex-right{
    justify-content: flex-end;
  }
}

.small-loader {
  max-height: 5px;
}

.sub-title-msg {
  font-size: 12px;
  color: $dark-gray;
}
