@import '../../styles/variables.scss';

.reason-wrapper {
  padding: 0 20px;

  .reason-card {
    padding: 10px;
    border: 1px solid $light-gray;
    background: #fff;
    border-radius: 4px;
    margin-bottom: 15px;
  }

  span.try {
    padding: 5px 10px;
    border-radius: 4px;
    text-align: center;
    background: $light-gray;
    display: inline-block;
    margin-bottom: 10px;
  }

  .absolute-sample {
    position: absolute;
    margin: auto;
    left: 79px;
    right: 0;
  }
}
