@import '../../styles/variables.scss';

.state-filter-button {
  display: inline-block;
  padding: 10px 25px;
  border: 1px solid #fff;
  border-top: 1px solid $gray-wrap;
  border-bottom: 1px solid $gray-wrap;
  position: relative;
  font-size: 14px;
  cursor: pointer;

  &:first-child {
    border-left: 1px solid $gray-wrap;
    border-right: 1px solid $gray-wrap;
    border-radius: 4px 0 0 4px;
  }

  &:nth-child(3) {
    border-right: 1px solid $gray-wrap;
    border-left: 1px solid $gray-wrap;
    border-radius: 0 4px 4px 0;
  }

  &.active {
    background-color: $filter-btn-gray;
    // border-right: 1px solid $gray-wrap;
    // border-left: 1px solid $gray-wrap;

    .arrow-bottom {
      display: inline-block;
    }
  }
}

// tables


.table-wrapper {
  overflow: auto;

  table {
    border-collapse: collapse;
    width: 100%;
  }

  tr.table-header {

    background-color: $lightest-gray;
    color: $dark-gray;
  }

  th.table-ele,
  td.table-ele {
    padding: 10px 15px;
    font-weight: normal;
    page-break-before: always;
    text-align: left;
  }

  tr {
    transition: all 0.1s linear;

    &:hover {
      background-color: $sushi-blue;
      ;
    }
  }

  tr.table-header {
    &:hover {
      background-color: $lightest-gray;
    }
  }

  -webkit-animation: fadein 0.4s;
  /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 0.4s;
  /* Firefox < 16 */
  -ms-animation: fadein 0.4s;
  /* Internet Explorer */
  -o-animation: fadein 0.4s;
  /* Opera < 12.1 */
  animation: fadein 0.4s;

}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

// pagination
.pagination-wrapper {
  margin-top: 20px;
  margin-bottom: 40px;
  padding: 0 20px;
  text-align: right;

  .icon {
    margin: 0 5px;
    color: $sushi-green-highlight;
    font-size: 18px;
    cursor: pointer;
  }
}

// loader
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-top: 40px;
}

.loader>div {
  width: 15px;
  height: 15px;
  background: $sushi-green;
  border-radius: 50%;
  margin: 3px;
  animation: loader 0.5s infinite alternate;
}

.loader>div:nth-child(2) {
  animation-delay: 0.2s;
}

.loader>div:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes loader {
  to {
    opacity: 0.1;
    transform: translate(0, -20px);
    background: $sushi-green;
    ;
  }
}

// modal
.modal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  padding: 40px 100px;
  left: 0;
  top: 0;
  z-index: 2500;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);

  .modal-overlay {
    background-color: #fff;
    width: 100%;
    padding: 40px;
    border-radius: 4px;

    .modal-title {
      font-size: 16px;
      font-weight: 600;
    }

    .modal-body {
      margin-top: 40px;
    }

    .close {
      position: fixed;
      cursor: pointer;
      font-size: 16px;
      color: $pri-red;
      margin-top: -25px;
      margin-left: calc(50% - 125px);

    }
  }

  .modal-picture {
    position: fixed;
    width: 100%;
    border-radius: 0;
    padding: 0;
    left: 0;
    bottom: 0;

    .modal-title {
      font-size: 16px;
      padding: 15px 20px;
      font-weight: 600;
    }

    .modal-body {
      margin-top: 0;
      padding: 15px 20px;
    }

    .close {
      position: fixed;
      cursor: pointer;
      font-size: 16px;
      color: $pri-red;
      margin-top: -25px;
      margin-left: calc(50% - 125px);

    }
  }

}

// date picker
/*!
 * https://github.com/YouCanBookMe/react-datetime
 */

.rdt {
  position: relative;
}

.rdtPicker {
  display: none;
  position: absolute;
  width: 250px;
  padding: 4px;
  margin-top: 1px;
  z-index: 99999 !important;
  background: #fff;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .1);
  border: 1px solid #f9f9f9;
}

.rdtOpen .rdtPicker {
  display: block;
}

.rdtStatic .rdtPicker {
  box-shadow: none;
  position: static;
}

.rdtPicker .rdtTimeToggle {
  text-align: center;
}

.rdtPicker table {
  width: 100%;
  margin: 0;
}

.rdtPicker td,
.rdtPicker th {
  text-align: center;
  height: 28px;
}

.rdtPicker td {
  cursor: pointer;
}

.rdtPicker td.rdtDay:hover,
.rdtPicker td.rdtHour:hover,
.rdtPicker td.rdtMinute:hover,
.rdtPicker td.rdtSecond:hover,
.rdtPicker .rdtTimeToggle:hover {
  background: #eeeeee;
  cursor: pointer;
}

.rdtPicker td.rdtOld,
.rdtPicker td.rdtNew {
  color: #999999;
}

.rdtPicker td.rdtToday {
  position: relative;
}

.rdtPicker td.rdtToday:before {
  content: '';
  display: inline-block;
  border-left: 7px solid transparent;
  border-bottom: 7px solid #428bca;
  border-top-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  bottom: 4px;
  right: 4px;
}

.rdtPicker td.rdtActive,
.rdtPicker td.rdtActive:hover {
  background-color: #428bca;
  color: #fff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
}

.rdtPicker td.rdtActive.rdtToday:before {
  border-bottom-color: #fff;
}

.rdtPicker td.rdtDisabled,
.rdtPicker td.rdtDisabled:hover {
  background: none;
  color: #999999;
  cursor: not-allowed;
}

.rdtPicker td span.rdtOld {
  color: #999999;
}

.rdtPicker td span.rdtDisabled,
.rdtPicker td span.rdtDisabled:hover {
  background: none;
  color: #999999;
  cursor: not-allowed;
}

.rdtPicker th {
  border-bottom: 1px solid #f9f9f9;
}

.rdtPicker .dow {
  width: 14.2857%;
  border-bottom: none;
  cursor: default;
}

.rdtPicker th.rdtSwitch {
  width: 100px;
}

.rdtPicker th.rdtNext,
.rdtPicker th.rdtPrev {
  font-size: 21px;
  vertical-align: top;
}

.rdtPrev span,
.rdtNext span {
  display: block;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
}

.rdtPicker th.rdtDisabled,
.rdtPicker th.rdtDisabled:hover {
  background: none;
  color: #999999;
  cursor: not-allowed;
}

.rdtPicker thead tr:first-child th {
  cursor: pointer;
}

.rdtPicker thead tr:first-child th:hover {
  background: #eeeeee;
}

.rdtPicker tfoot {
  border-top: 1px solid #f9f9f9;
}

.rdtPicker button {
  border: none;
  background: none;
  cursor: pointer;
}

.rdtPicker button:hover {
  background-color: #eee;
}

.rdtPicker thead button {
  width: 100%;
  height: 100%;
}

td.rdtMonth,
td.rdtYear {
  height: 50px;
  width: 25%;
  cursor: pointer;
}

td.rdtMonth:hover,
td.rdtYear:hover {
  background: #eee;
}

.rdtCounters {
  display: inline-block;
}

.rdtCounters>div {
  float: left;
}

.rdtCounter {
  height: 100px;
}

.rdtCounter {
  width: 40px;
}

.rdtCounterSeparator {
  line-height: 100px;
}

.rdtCounter .rdtBtn {
  height: 40%;
  line-height: 40px;
  cursor: pointer;
  display: block;

  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
}

.rdtCounter .rdtBtn:hover {
  background: #eee;
}

.rdtCounter .rdtCount {
  height: 20%;
  font-size: 1.2em;
}

.rdtMilli {
  vertical-align: middle;
  padding-left: 8px;
  width: 48px;
}

.rdtMilli input {
  width: 100%;
  font-size: 1.2em;
  margin-top: 37px;
}

.rdtTime td {
  cursor: default;
}

.react-images__blanket,
.react-images__positioner {
  z-index: 10000 !important;
}

.reason-list {
  padding: 0 0 15px 0;
  margin-bottom: 20px;
  position: relative;
  border-bottom: 1px solid $lightest-gray;

}
