@import '../../styles/variables.scss';

.home-wrapper {
    // position: relative;

    .home-intro-wrapper {
        padding-top: 20vh;
        color: $pri-black;
        z-index: 2;
        position: relative;
        @media (max-width: $sm-screen) {
           padding-top: 50px;
        }
        .home-head {
            font-size: 44px;
            font-weight: 400;
            @media (max-width: $sm-screen) {
               font-size: 32px;
            }
        }

        .home-para {
            margin-top: 10px;
            font-size: 44px;
            font-weight: 200;
            @media (max-width: $sm-screen) {
               font-size: 32px;
            }
        }
    }

    .banner-food {
        width: 100%;
        overflow: hidden;
        img {
            width: 100%;
        }
    }

    .banner-food-mobile{
        position: absolute;
        top: -100px;
        opacity: 0.8;
        right: -40%;
    }
}

.intro-graph-1,
.intro-graph-2,
.intro-graph-3 {
    position: absolute;
    opacity: 0.3;
    width: auto;
    display: inline-block;
    left: 0;
    top: 0;
    overflow: hidden;
    transform: rotate(4deg);
    // transition: all 0.2s linear;

    img {
        width: auto;
    }
}

.intro-graph-3 {
    right: 0;
    left: auto;
    margin-top: -50px;
    text-align: right;
}

.intro-graph-2 {
    left: 0;
    display: inline-block;
    width: auto;
    height: 100px;
    bottom: 0;
    text-align: left;
    position: absolute;
    top: 120px;
}

.intro-wrapper {
    min-height: calc(100vh + 110px);
    position: relative;
    overflow: hidden;

}

.intro-overlay-graph {
    background-color: rgba(0, 0, 0, 0);
    transform: rotate(-4deg);
    width: 100%;
    margin-top: -200px;
    overflow: hidden;
    height: 200px;
    position: relative;
        width: 110vw;
        left: 0;
    bottom: 0;
    top: 0;
    display: block;
}
